nav {
   position: absolute;
   display: flex;
   top: 2%;
}

img {
   margin-left: 10vw;
   width: 12vw;
}

.nav-links {
   margin-left: 20vw;
   display: grid;
   text-align: center;
}

.nav-link {
   font-size: 45px;

   height: fit-content;
   top: 0; bottom: 0;
   margin: auto;
   
   background-color: brown;
   border-radius: 30px;
   padding: 20px;

   cursor: default;

   transition: all 0.7s;
}

.nav-link:hover {
   color: chartreuse;
}